:root {
  --primary-color: rgb(30, 30, 30);
  --light-primary-color: rgb(30, 30, 30, 0.1);
  --header-height: 89px;
}

a {
  color: unset;
  text-decoration: none;
}

a:-webkit-any-link {
  color: unset;
}

a:hover {
  text-decoration: underline;
}

ul {
  list-style: none;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page {
  padding: var(--header-height) 0;
  max-width: 1440px;
  margin: 0 auto;
}

header {
  position: fixed;
  width: 100%;
  z-index: 2;

  
  nav {
      max-width: 1440px;
      margin: 12px auto 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 32px;
      background-color: var(--light-primary-color);
      backdrop-filter: blur(128px);
      -webkit-backdrop-filter: blur(128px);
      border-radius: 6px;

      .logo img {
          width: 60px;
      }

      .nav-menu ul{
          list-style: none;
          display: flex;
          gap: 16px;
          color: var(--primary-color);
      }
  }
}

.hero {
  position: relative;
  margin-top: 12px;
  width: 1440px;
  height: 500px;
  background-image: url('./assets/hero.jpg');
  

  p {
    position: absolute;
    top: 10%;
    left:0;
    right: 0;
    text-align: center;
    color: #fff;
    font-size: 80px;
  }
  button {
    cursor: pointer;
    position: absolute;
    bottom: 30%;
    left: 50%;
    transform: translateX(-50%);
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff;
    padding: 18px 24px;
    font-size: 32px;
  }
  button:hover {
    background-color: #fff;
    color: var(--primary-color);
  }

}

.login {

  display: flex;
  justify-content: center;
  
  .loginbtn {
    border: 1px solid var(--primary-color);
    border-radius: 6px;
    width: fit-content;
    display: flex;
    gap: 8px;
    align-items: center;
    justify-content: center ;
    padding: 0 12px;
    cursor: pointer;
    margin-top: var(--header-height);

    img {
      width: 32px;
      height: auto;
      
    }
  }

  .loginbtn:hover {
    background-color: var(--primary-color);
    color: #fff;
  }
}

.product-card-box, .shop-products {
  // display: grid;
  // grid-template-columns: repeat(4, minmax(0, 1fr));
  // gap: 20px;
  display: flex;
  flex-wrap: wrap;
  max-width: 916px;
  gap: 48px;
  width: calc(100% - 20px);
  margin: 0 auto;
  max-width: 1440px;
}

.productcard-wrap {
  height: 260px;
  width:300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 6px;
  border: 1px solid var(--primary-color);
  cursor: pointer;



  .productcard {
    position: relative;
    width: 90%;
    padding-top: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
      .productcard-description {
        display: flex;
        justify-content: space-between;
        padding: 12px 12px 0 12px;
        font-size: large;
        font-weight: 700;
      }
      .productcard-image {
        object-fit: contain;
        height: 200px;

      }
      
    }

    button {
      background-color: var(--primary-color);
      border: 1px solid var(--primary-color);
      color: #fff;
      border-radius: 6px;
      border: none;
      font-size: 16px;
      padding: 2px 8px;
      cursor: pointer;
      box-shadow: 2px 2px 24px -4px rgba(0,0,0,0.45);
-webkit-box-shadow: 2px 2px 24px -4px rgba(0,0,0,0.45);
    }

    button:hover {
      background-color: #fff;
      color: var(--primary-color);
      border: 1px solid var(--primary-color);
    }
}

.productpage {
  .operate-control {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .operate-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid var(--primary-color);
      padding: 0;
      width: 18px;
      height: 18px;
      cursor: pointer;
      border-radius: 4px;
      background-color: #f1eaea;
  }
  .operate-btn:hover {
    background-color: #fff;
  }

  .addtocart-btn {
      background-color: var(--primary-color);
      border: 1px solid var(--primary-color);
      color: #fff;
      border-radius: 6px;
      border: none;
      font-size: 16px;
      padding: 2px 8px;
      cursor: pointer;
      box-shadow: 2px 2px 24px -4px rgba(0,0,0,0.45);
-webkit-box-shadow: 2px 2px 24px -4px rgba(0,0,0,0.45);
margin-top: 20px;
  }
  .addtocart-btn:hover {
    background-color: #fff;
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
  }
}

.cartpage {
  display: flex;
}

.cartitem-row {
  display: flex;
  margin-top: 16px;
  gap: 32px;
  align-items: center;

  .cartitem-control {
    display: flex;
    align-items: center;
    gap: 8px;

    button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--primary-color);
    padding: 0;
    width: 18px;
    height: 18px;
    cursor: pointer;
    border-radius: 4px;
    background-color: #f1eaea;
    }
    button:hover {
      background-color: #fff;
    }
  }

  .cartitem-delete {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--primary-color);
    width: 20px;
    height: 20px;
    padding: 0;
    cursor: pointer;
    border-radius: 4px;
  }

  .cartitem-delete:hover {
    color: white;
    background-color: var(--primary-color);
  }

  .cartitem-image {
    height: 150px;
    object-fit: contain;
  }
}

.cartreview {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 100px;

  p {
    font-size: 28px;
    font-weight: bold;
  }
  button {
    width: 100%;
    background-color: var(--primary-color);
    color: #fff;
    border: 1px solid var(--primary-color);
    padding: 12px 0;
    cursor: pointer;
    margin-bottom: 24px;
  }
}

.shop-page {
  display: flex;
  .shop-cat {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 32px 0 24px;

    ul li{
      margin-bottom: 12px;
    }
  }
  .shop-products {
    padding-top: 1.8rem;
    display: flex;
  }
}

footer {
  background-color: var(--light-primary-color);
  .footer-wrap {
    max-width: 1440px;
    margin: 0 auto;
    .totopbar {
      display: flex;
      justify-content: center;
      cursor: pointer;
      color: var(--primary-color);
      padding: 12px 0;
    }
    .footer-container {
      display: flex;
      justify-content: space-around;
      align-items: center;
      gap: 16px;

    }
  }

  .logo img {
    width: 100px;
}
}